.emptyDivBurguerOpened {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    height: 108px;

}
.hideElement {
    display: none;
}

.backgroundDiv {
    box-sizing: border-box;
    background-color: #000000;
    height: 108px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0px 32px;
    min-width: fit-content;
    width: 100%;
  }
.divLinks{
    width: fit-content;
    padding: 0px 16px;
    display: grid;
    display: grid;
    grid: 20px / auto auto auto auto auto ;
    grid-column-gap: 32px;
}
.textDefaultNav{
    width: fit-content;
    font-family: 'SEATBCN-Regular';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
text-decoration: none;
height: 20px;
/* identical to box height, or 125% */

text-align: center;

/* Neutral/White 100 */

color: #FFFFFF; 
  }
.textDefaultNav:hover{
    color: #FE3B1F; 
}
.textSelectedNav {
    width: fit-content;
    font-family: 'SEATBCN-Regular';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
text-decoration: none;
height: 20px;
/* identical to box height, or 125% */

text-align: center;

/* Neutral/White 100 */

color: #FE3B1F; 
}
.linkCart {
    text-decoration: none;
}
.carritoContainer {
    
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
    
}
.svgContainer{
    position: relative;
    height: 28px;
    width: 28px;
    justify-content: center;
    align-items: center;
}
.carritoText {
    text-decoration: none;
    margin: 0px 8px 0px 0px;
}
.nroCarritoText {
    margin: 6px 0px;
    height: 16px;
    width: 28px;
    position: absolute;
    top: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'SEATBCN-Bold';
    color: #FFFFFF;
}
.burguerIcon{
    display: initial;
    cursor: pointer;
}
.wrapper {
    min-width: fit-content;
    width: 100%;
}
.WrapperburguerOpened {
    display: initial;
}
.WrapperburguerClosed {
    position: initial;
}
.burguerOptionsGridOpened {
    width: 100%;
    min-width: fit-content;
    background-color: transparent;
    display: grid;
    grid: 1fr / repeat(6, minmax(0, 1fr)); ;
    grid-column-gap: 32px;
    box-sizing: border-box;
    border-left-style: solid;
    border-left-color: #000000;
    border-width: 32px;
    border-right-style: solid;
    border-right-color: transparent;
    overflow: hidden;
}
.burguerOptionsGridClosed {
    display: none
}
.burguerOptionsOpened {
    box-sizing: border-box;
    background-color: #000000;
    height: 100vh;
    min-width: fit-content;
    width: 100%;
    grid-area: 1 / 1 / span 1/ span 3;
    padding: 32px;
    padding-left: 0px;
}

.optionBurguerLink {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 32px;
    font-family: 'SEATBCN-Bold';
    font-style: normal;
    font-weight: 750;
    font-size: 30px;
    line-height: 32px;
    color: #FFFFFF; 
    text-decoration: none;
}
.optionBurguerLink:hover{
    color: #FE3B1F; 
}
.showBackToMenuBurguer {
    padding: 32px 20px;
    background-color: #000000;
    
    display: flex;
    align-items: center;
    cursor: pointer;
}
.arrowIconBackToMenu {
    height: 28px;
    width: 28px;
    margin-right: 16px;

}
.textBackToMenu{
    font-family: 'SEATBCN-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
/* identical to box height, or 125% */

display: flex;
align-items: center;
text-align: center;

/* Brand/Orange */

color: #FE3B1F;
}
@media (max-width: 360px) {
    
    .burguerOptionsGridOpened {
        grid: 1fr / 1fr 1fr 1fr 1fr;
        grid-column-gap: 20px;
        box-sizing: border-box;
        border-left-style:none;
        border-left-color: transparent;
        border-width: 0px;
        background-color: #000000;
        padding: 20px;
        }
    .burguerOptionsOpened {
        padding: 0px;
        grid-area: 1 / 1 / span 1/ span 4;
    }
}
@media (max-width: 768px) {
    .carritoText{
        display: none;
    }
    .divLinks{
        
        display: none;
    }
    
    .WrapperburguerOpened{
        position: fixed;
        top: 0;
        width: 100%;}
    
}
@media (min-width: 768px) {
    .showBackToMenuBurguer{
        display: none;
    }
    .burguerIcon{
        display: none;
        
    }
    .burguerOptionsGridOpened {
        display: none;
        }
    .emptyDivBurguerOpened{
        display: none;
    }
}